import React from 'react';
import {InputGroup, InputGroupAddon, Input, Row, Col, Container, FormGroup, Label, InputGroupText} from 'reactstrap';

class App extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            a: 0,
            b: 0,
            P_m: 0,
            isPaint: false,
            grav_price: 0,
            rez_price: 0,
            materialName: '',
            S_gr: 0,
            discount: 0,
            L_rez: 0,
            urgently: 0
        };
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
        const target = event.target;
        const value = target.value;
        const name = target.name;

        this.setState({
            [name]: Number(value)
        });

        if (name === 'P_m') {
            this.setState({
                materialName: event.target.attributes.getNamedItem('data-key').value
            });
        }
    }

    getMaterials = (materials) => {
        let materials_list = [];

        for (let key in materials) {
            if (materials.hasOwnProperty(key)) {
                materials_list.push(<FormGroup className='mb-2' key={key} check>
                    <Label check>
                        <Input key={key} data-key={key} type="radio" value={materials[key]} name="P_m"
                               onChange={this.handleChange}/>{' '}
                        {key}</Label>
                </FormGroup>)
            }
        }
        return materials_list;
    };

    getGrav_price = (grav_prices) => {
        for (let price in grav_prices) {
            if (grav_prices.hasOwnProperty(price)) {
                if ((this.state.materialName).indexOf(price) !== -1) {
                    return grav_prices[price]
                }
            }
        }
        return -1
    };

    getRez_price = (rez_prices) => {
        for (let price in rez_prices) {
            if (rez_prices.hasOwnProperty(price)) {
                if ((this.state.materialName).indexOf(price) !== -1) {
                    return rez_prices[price]
                }
            }
        }
        return -1
    };

    getDiscountValue = () => {
        return precisionRound(((this.getMaterialPrice() +
            this.getRezPrice() +
            this.getGravPrice() +
            this.getPaintingPrice()) * this.state.discount) / 100, 2)
    };

    getUrgentPrice = () => {
        return precisionRound(((this.getMaterialPrice() +
            this.getRezPrice() +
            this.getGravPrice() +
            this.getPaintingPrice()) * this.state.urgently) / 100, 2)
    };

    getMaterialPrice = () => precisionRound(this.state.a * this.state.b * this.state.P_m, 2);

    getMaterialSquare = () => precisionRound(this.state.a * this.state.b, 3);

    getRezPrice = () => (this.getRez_price(this.props.data.rez_prices, this.props.data.materials) === -1) ? 0 : precisionRound(this.getRez_price(this.props.data.rez_prices, this.props.data.materials) * this.state.L_rez, 2);

    getGravPrice = () => (this.getGrav_price(this.props.data.grav_prices, this.props.data.materials) === -1) ? 0 : precisionRound(this.getGrav_price(this.props.data.grav_prices, this.props.data.materials) * this.state.S_gr, 2);

    getPaintingPrice = () => (this.state.isPaint) ? precisionRound(this.state.a * this.state.b * this.props.data.painting, 2) : 0;


    render() {
        let {discount, urgently, materials} = this.props.data;

        return (
            <Container fluid>
                <Row>
                    <Col sm='6'>
                        <h3 className='mt-3'> </h3>
                        <FormGroup className='mb-2'>
                            <Label className='mb-1'> </Label>
                            <InputGroup size="sm">
                                <Input placeholder="0" type="number" step="0.01" min='0' name='L_rez'
                                       onChange={this.handleChange}/>
                                <InputGroupAddon addonType="append">/</InputGroupAddon>
                            </InputGroup>
                        </FormGroup>
                        <FormGroup className='mb-2'>
                            <Label className='mb-1'> , <sup>2</sup></Label>
                            <InputGroup size="sm">
                                <Input placeholder="0" type="number" step="0.01" min='0' name='a'
                                       onChange={this.handleChange}/>
                                <Input placeholder="0" type="number" step="0.01" min='0' name='b'
                                       onChange={this.handleChange}/>
                                <InputGroupAddon addonType="append">.</InputGroupAddon>
                            </InputGroup>
                        </FormGroup>
                        <FormGroup className='mb-2'>
                            <Label className='mb-1'> </Label>
                            <InputGroup size="sm">
                                <Input placeholder="0" type="number" step="1" min='0' name='S_gr'
                                       onChange={this.handleChange}/>
                                <InputGroupAddon addonType="append">
                                    <InputGroupText><sup>2</sup></InputGroupText>
                                </InputGroupAddon>
                            </InputGroup>
                        </FormGroup>
                    </Col>
                    <Col sm='6' md='3' className='border- border-light'>
                        <h3 className='mt-3'></h3>
                        {this.getMaterials(materials)}
                    </Col>

                    <Col sm='12' className='border-top border-light my-3 d-lg-none'></Col>

                    <Col lg='3' className='order-3 order-lg-1'>
                        <h3 className='mt-3'></h3>
                        <div>
                            <Label> :&nbsp;</Label><Label
                            className='fw-900'>{this.getMaterialSquare()}</Label><Label className='fw-900'><sup>2</sup></Label>
                        </div>
                        <div>
                            <Label> :&nbsp;</Label><Label
                            className='fw-900'>{this.getMaterialPrice()}</Label><Label className='fw-900'>.</Label>
                        </div>
                        <div>
                            <Label> :&nbsp;</Label><Label className='fw-900'>{this.getRezPrice()}</Label><Label
                            className='fw-900'>.</Label>
                        </div>
                        <div>
                            <Label> :&nbsp;</Label><Label className='fw-900'>{this.getGravPrice()}</Label><Label
                            className='fw-900'>.</Label>
                        </div>
                        <div>
                            <Label> ({this.state.discount}%):&nbsp;</Label><Label
                            className='fw-900'>{this.getDiscountValue()}</Label><Label className='fw-900'>.</Label>
                        </div>
                        <div>
                            <Label> :&nbsp;</Label><Label
                            className='fw-900'>{this.getPaintingPrice()}</Label><Label className='fw-900'>.</Label>
                        </div>
                        <div>
                            <Label>:&nbsp;</Label><Label
                            className='fw-900'>{this.getUrgentPrice()}</Label><Label className='fw-900'>.</Label>
                        </div>
                        <div className='mt-2'>
                            <h6>:&nbsp;<span>
                                {precisionRound(
                                    this.getMaterialPrice() +
                                    this.getRezPrice() +
                                    this.getGravPrice() +
                                    this.getPaintingPrice() -
                                    this.getDiscountValue() +
                                    this.getUrgentPrice(),
                                    2)
                                }
                            </span><span>.</span></h6>
                        </div>
                    </Col>

                    <Col sm='12' className='border-top border-light my-3 order-2'></Col>

                    <Col sm='6' className='order-1 order-lg-3'>
                        <FormGroup>
                            <h3 className=''> </h3>
                        </FormGroup>
                        <FormGroup>
                            <Label for="exampleSelect"></Label>
                            <Input bsSize="sm" type="select" value={this.state.isPaint} id="exampleSelect"
                                   name='isPaint' onChange={this.handleChange}>
                                <option value={0}></option>
                                <option value={1}></option>
                            </Input>
                        </FormGroup>
                        <FormGroup className='mb-2'>
                            <Label className='mb-1'></Label>
                            <Input bsSize="sm" type="select" name="discount" onChange={this.handleChange}>
                                {getDiscount(discount)}
                            </Input>
                        </FormGroup>
                        <FormGroup className='mb-2'>
                            <Label className='mb-1'> </Label>
                            <Input bsSize="sm" type="select" name="urgently" onChange={this.handleChange}>
                                <option value='0'></option>
                                <option value={urgently}></option>
                            </Input>
                        </FormGroup>
                    </Col>


                </Row>
            </Container>
        );
    }
}

const getDiscount = (discount) => {
    let options = [];

    for (let key in discount) {
        if (discount.hasOwnProperty(key)) {
            options.push(<option value={discount[key]} key={key}>{key}</option>)
        }
    }
    return options;
};

function precisionRound(number, precision) {
    let factor = Math.pow(10, precision);
    return Math.round(number * factor) / factor;
}

export default App;


